import React from 'react'
import { Link, graphql, StaticQuery } from 'gatsby'
import SearchBox from '../SearchBox'
import newpatientform from '../../../static/New Patient Form.pdf'

const NavBar = ({ toggleNavbar, isActive }) => (
  <StaticQuery
    query={graphql`
            query SearchIndexQuery {
                siteSearchIndex {
                    index
                }
            }
        `}
    render={data => (
      <nav className='navbar is-fixed-top' aria-label='main navigation'>
        <div className='navbar-brand'>
          <Link to='/' className='navbar-item'>
            <strong>Dearborn Endocrinology</strong>
          </Link>
          <button
            className={`button navbar-burger ${isActive ? 'is-active' : ''}`}
            data-target='navMenu'
            onClick={toggleNavbar}
          >
            <span />
            <span />
            <span />
          </button>
        </div>
        <div className={`navbar-menu ${isActive ? 'is-active' : ''}`} id='navMenu'>
            <Link className='navbar-item' to='/OurStaff'>
                            Staff
            </Link>
            <Link className='navbar-item' to='/practiceFields'>
                            Fields of Practice
            </Link>
            <Link className='navbar-item' to='/location'>
                            Location
            </Link>
            <Link className='navbar-item' >
                            Download New Patient form
            </Link>
            {/*<Link className='navbar-item' to='/contact'>
                            Contact Us
            </Link>*/}
        </div>
      </nav>
    )}
  />
)

export default NavBar
